.slider {
    display: flex;
    align-items: center;
    margin: 20px;

    opacity: 0;
    transform: scale(0.85);
    transition: 2s;
}

.slider--zoom {
    opacity: 1;
    transform: scale(1);
    transition: 2s;
}

.slider_image {
    width: 60%;
}

.slider_content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    text-align: center;
}

.slider_title {
    text-align: center;
}

@media screen and (max-width: 800px) {
    .slider {
        flex-direction: column;
        background-color: rgb(236, 236, 236);
    }
    .slider_image{
        width: 100%;
    }
}
